.header-color {
  background: #8E0E00;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #1F1C18, #8E0E00);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #1F1C18, #8E0E00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid{
  background: #8E0E00;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #1F1C18, #8E0E00);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #1F1C18, #8E0E00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */



  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.avatar-img{
  border-radius: 50%;
  height: 250px;
  padding-top: 60px;
}
.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  text-align: center;
}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}
.banner-text h2{
  font-size: 40px;
  font-weight: bold;
  color: white;
}
.banner-text hr{
  border-top: 5px dotted white;
  width:50%;
  margin: auto;
}
.banner-text p{
  color: white;
  font-size: 20px;
  padding: 10px;
}
.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-links i{
  color: white;
  font-size: 50px;
  padding:10px;
}
.Projects-grid{
  display: flex;
  padding-top: 50px;
}
.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 25px;
}
.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}
.contact-grid h2{
  font-family: 'Anton', san-serif;
}
.contact-grid p{
  font-family: 'Oxygen', san-serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}
.contact-list i{
  font-size: 50px;
  padding-right: 80px;
}
.contact-list{
 
  text-align: center;
}
.resume-right-col{
  background: #27221f;
  color: white;
  padding: 40px;
}
.col-right p{
  font-weight: bold;
}
.col-right h5{
  font-weight: bold;
}
.about-me{
  text-align: center;
}
